import React, {useState} from 'react';

import Modal from './Modal';

import './styles/Estudio.css';


function Estudio(props) {

const [state, setState] = useState({
    modalIsOpen: false,
});

const handleOpen = e => {
    setState({modalIsOpen: true})
}

const handleClose = e => {
    setState({modalIsOpen: false})
}

    return (
        <li>
            <h5>{props.name}</h5>
            <p>En: <span>{props.place}</span></p>
            <p >Certificado: <button className="verAqui" onClick={handleOpen}>Ver Aqui</button></p>
            <div id="modal">
                <Modal isOpen={state.modalIsOpen} onClose={handleClose}> 
                <div>
                    <img className="certificate" src={props.certificate} alt="" />
                </div>
                </Modal>
            </div>
        </li>
    )
}

export default Estudio;