import React from 'react';

import Estudio from '../components/Estudio';

import programacion from '../images/certificates/programacion.jpg';
import software from '../images/certificates/software.jpg';
import designThinking from '../images/certificates/designThinking.jpg';
import studying from '../images/studying.svg';



import '../styles/Estudios.css';

function Estudios() {
    return (
        <div className="estudios">
            <h2 className="estudios-title">Mis estudios formales</h2>
            <div className="estudios__container">
                <ul className="estudios__listado">
                    <Estudio 
                        name="Fundamentos TIC para profesionales de negocios: Programación"
                        place="Universidad Politecnica de Valencia mediante edX"
                        certificate={programacion}
                    />
                    <Estudio 
                        name="Fundamentos TIC para profesionales de negocios: Desarrollo de Software"
                        place="Universidad Politecnica de Valencia mediante edX"
                        certificate={software}
                    />
                    <Estudio 
                        name="Lánzate a la Innovación con Design Thinking"
                        place="Pontificia Universidad Javeriana mediante edX"
                        certificate={designThinking}
                    />
                    <Estudio 
                        name="Python de la A a la Z"
                        place="Universidad Anáhuac mediante edX"
                        certificate={designThinking}
                    />
                </ul>
                <div className="studying"> 
                    <img src={studying} alt="" />
                </div>
            </div>
        </div>
    )
}

export default Estudios;