import React from 'react';

import Proyecto from '../components/Proyecto';

import agendaWeb from '../images/projects/agendaWeb.png';

import '../styles/Proyectos.css';


function Proyectos() {
    return (
        <div className="">
            <h2 className="Proyectos-title">Mis últimos proyectos</h2>
            <div className="proyectos">
                <div className="proyecto">
                    <Proyecto 
                        imagen={agendaWeb} 
                        nombre="Agenda Web" 
                        tecnologias="HTML | CSS | JavaScript | React" 
                        info="Una Agenda en una Single Page App hecha con React" 
                    />
                </div>
            </div>
        </div>
    )
}

export default Proyectos;