import React from 'react';

import './styles/Proyecto.css';

function Proyecto(props) {
    return(
        <div className="Proyecto">
            <img src={props.imagen} alt="" />
            <div className="Proyecto__info-container">
                <div className="Proyecto__info">
                    
                    <h4 className="Poyecto__info-title">{props.nombre }</h4>
                    
                    <p>{props.tecnologias}</p>
                    <p>{props.info}</p>
                </div>
            </div>
            <div className="Proyecto__buttons">
                <a href="https://s-arzuaga.github.io/agendaweb/" target="_blank" rel="noreferrer" className="btn btn-primary">Ver Proyecto</a>
            </div>
        </div>
    )
}

export default Proyecto;