import React from "react";

import Skill from "../components/Skill";

import "../styles/Skills.css";

import html from "../images/icons/html.svg";
import css from "../images/icons/css.svg";
import javaScript from "../images/icons/javaScript.svg";
import react from "../images/icons/react.svg";
import mysql from "../images/icons/mysql.svg";
import node from "../images/icons/node.svg";

function Skills() {
  return (
    <div>
      <h2 className="Skills-title">Skills</h2>
      <div className="Skills">
        <div className="Skill">
          <Skill image={html} name="HTML5" />
        </div>
        <div className="Skill">
          <Skill className="css" image={css} name="CSS3" />
        </div>
        <div className="Skill">
          <Skill image={javaScript} name="JavaScript " />
        </div>
        <div className="Skill">
          <Skill image={react} name="React " />
        </div>
        <div className="Skill">
          <Skill image={mysql} name="MySQL " />
        </div>
        <div className="Skill ">
          <Skill image={node} name="Node.js " />
        </div>
      </div>
    </div>
  );
}

export default Skills;
