import React from "react";

import "../styles/AboutMe.css";

import avatar from "../images/imagen_cv.jpeg";
import working from "../images/working.svg";

function AboutMe() {
  return (
    <div className="info">
      <div className="name">
        <img src={avatar} alt="" />
        <div>
          <h2>Santiago Arzuaga Arias</h2>
          <h4>Desarrollador Junior</h4>
          <div className="location">
            <svg
              xmlns="http://www.w3.org/2000/svg"
              class="icon icon-tabler icon-tabler-pin"
              width="72"
              height="72"
              viewBox="0 0 24 24"
              stroke-width="1.5"
              stroke="#5e8eff"
              fill="none"
              stroke-linecap="round"
              stroke-linejoin="round"
            >
              <path stroke="none" d="M0 0h24v24H0z" fill="none" />
              <path d="M15 4.5l-4 4l-4 1.5l-1.5 1.5l7 7l1.5 -1.5l1.5 -4l4 -4" />
              <line x1="9" y1="15" x2="4.5" y2="19.5" />
              <line x1="14.5" y1="4" x2="20" y2="9.5" />
            </svg>
            <p>Ciudad de la Costa, Canelones</p>
          </div>
          <p className="col">
            <span>Correo:</span> santiago.arzuaga.dev@gmail.com
          </p>
          <p className="col">
            <span>Celular:</span> 093304445
          </p>
        </div>
      </div>
      <div className="summary">
        <div className="img">
          <img src={working} alt="" />
        </div>
        <p> Soy un estudiante autodidacta apasionado por el desarrollo web.</p>
        <p>
          Me encuentro en constante formación, capacitandome en nuevas
          tecnologias y perfeccionandome en las ya aprendidas.
        </p>
      </div>
    </div>
  );
}

export default AboutMe;
